/* iPhone XS Max, XR ----------- */
@media only screen and (min-device-width: 414px) and (max-device-height: 896px) and (orientation: portrait) and (-webkit-device-pixel-ratio: 2) {
  .body .intro-container {
    margin-bottom: 0 !important;
  }

  .form-container {
    max-height: 538px !important;
    top: -21px !important;
    margin-right: 25pt;
    width: 72%;
    left: 16px;
    text-align: center;
    input {
      width: 229px !important;
    }
    .form-title {
      font-size: 50px !important;
      &.little {
        font-size: 18px !important;
      }
    }
    .label-container {
      text-align: left;
      margin-left: 19px;
    }
  }
  .specification-container {
    text-align: left;
    width: 74%;
    margin: 0 auto;
    margin-left: 85px;
    ul {
      text-align: left;
      position: relative;
      right: 39px;
    }
  }
  .project-tech-container {
    .project-title {
      width: 321px !important;
      margin-left: 46px !important;

      &.industry {
        min-width: auto !important;
      }
    }
  }
  .team-container {
    &.team {
      .MuiGrid-item:nth-child(2) {
        margin-top: 20px;
      }
      .team-text {
        margin-left: 60px !important;
      }
    }
    &.industry {
      .MuiGrid-item {
        padding: 20px !important;
        max-width: 90%;
        margin: 0 auto;
        margin-left: 49px;
      }
    }
    &.partners {
      img {
        max-width: 100%;
      }
      .partner-title {
        right: 0 !important;
      }
    }
  }

  .project-tech-container {
    .team-container {
      &.partners {
        .right-partners-container {
          padding-top: 0 !important;
          .team-text {
            text-align: center !important;
          }
        }
        .partners-container-button {
          left: 6px;
          position: relative !important;
          bottom: 0px !important;
        }
      }
    }
  }
  .brands-container {
    width: 95% !important;
  }
  .footer-container {
    padding: 30px 15px !important;
    text-align: center;
    .text-bottom {
      position: inherit !important;

      &.top {
        line-height: 2.69 !important;
      }
    }
    .text-bottom-container {
      .text-bottom.bottom {
        position: relative !important;
        bottom: 0;
      }
    }
  }
}

