@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');
@import '../../media-queries.scss';

.header {
  background-image: url('../../assets/bitmap@2x.png');
  background-repeat: no-repeat, repeat;
  background-position: center;
  background-size: cover;
  min-height: 784px;
}

.logo-container {
  padding: 25px 19px;
}

h1,
h2,
h3 {
  margin-block-start: 0px !important;
  margin-block-end: 0px !important;
}

.form-container {
  max-width: 622px;
  max-height: 487px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  position: relative;
  top: 147px;
  right: 0px;
  padding: 5px 34px 25px;
  .form-title {
    font-family: 'Montserrat';
    font-size: 61px;
    color: #626262;

    &.orange {
      color: #fb6c03;
    }
    &.little {
      font-family: 'Open Sans', sans-serif;
      font-size: 18px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.72;
      letter-spacing: normal;
      font-weight: 700;
      color: #444444;
    }
  }
  .Mui-focused {
    .MuiOutlinedInput-notchedOutline {
      border-color: rgba(0, 0, 0, 0.23) !important;
    }
  }

  .MuiTextField-root {
    label {
      display: none;
    }
  }
  .MuiInputBase-formControl {
    height: 40px;
    margin-bottom: 10px;
    fieldset {
      outline: 0 !important;
      legend {
        outline: 0 !important;
        display: none !important;
      }
    }
  }
  input {
    width: 200px;
    max-height: 40px;
    border-radius: 2px;
    margin-bottom: 10px;
  }

  span.label {
    width: 180px;
    height: 18px;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    color: #444444;
  }

  .schedule-button {
    margin-top: 10px;
    width: 218px;
    height: 40px;
    background-color: #fb6c03;
    color: #ffffff;
    border-radius: 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    text-transform: unset !important;
    &:hover {
      cursor: pointer;
      background-color: #f57e23;
    }
  }
}

.body {
  .body-intro {
    font-family: 'Montserrat';
    font-size: 33px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.76;
    letter-spacing: normal;
    text-align: center;
    color: #444444;
  }
  .intro-container {
    margin: 70px auto;
    width: 72%;
  }
  .specifications {
    margin: 0 auto;
    max-width: 1200px;
    .specification-container {
      .spec-title {
        font-family: 'Montserrat';
        font-size: 25px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.32;
        letter-spacing: normal;
        color: #4d4d4d;
      }
      li {
        list-style-type: none;
        font-family: 'Open Sans', sans-serif;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.69;
        letter-spacing: normal;
        color: #444444;
        .MuiSvgIcon-root {
          width: 24px;
          height: 24px;
          position: relative;
          top: 5px;
          margin-right: 5px;
          color: #fb6c03;
        }
      }
    }
  }
}

.project-tech-container {
  .project-title {
    width: 534px;
    margin: 0 auto;
    h2 {
      font-family: 'Montserrat';
      font-size: 33px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.76;
      letter-spacing: normal;
      text-align: center;
      color: #444444;
    }
    &.industry {
      min-width: 606px;
      margin-top: 70px;
    }
    &.partners {
      text-align: center;
    }
  }
  .team-container {
    max-width: 774px;
    margin: 0 auto;
    text-align: center;
    .team-text {
      font-family: 'Open Sans', sans-serif;
      font-size: 16px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.69;
      letter-spacing: normal;
      text-align: center !important;
      color: #444444;
      width: 314px;
      margin-left: 60px;
    }
    h3:not(.MuiButton-label) {
      font-family: 'Montserrat';
      font-size: 25px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.32;
      letter-spacing: normal;
      text-align: center;
      color: #4d4d4d;
    }
    &.industry {
      max-width: 890px;
      .logos {
        margin: 0 !important;
      }
    }
    &.partners {
      max-width: 1200px;
      width: 1200px;
      .team-text {
        margin: 20px auto;
        width: 387px;
        text-align: left !important;
        height: max-content;
      }

      .partner-title {
        position: relative;
        right: 62px;
      }
      .right-partners-container {
        padding-top: 86.7px;
      }

      .partners-container-button {
        position: relative;
        bottom: 40px;
        right: 90px;
        .schedule-button {
          width: 218px;
          height: 40px;
          background-color: #fb6c03;
          color: #ffffff;
          border-radius: 20px;
          font-family: 'Open Sans', sans-serif;
          font-size: 14px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: center;
          text-transform: unset !important;
          &:hover {
            cursor: pointer;
            background-color: #f57e23;
          }
        }
      }
    }
    &.team {
      display: flex;
      justify-content: space-between;
      width: 833px;
      max-width: 1200px;
    }
  }
}

.brands-container {
  max-width: 1200px;
  margin: 58px auto;
  width: 90%;
  text-align: center;
}

.footer-container {
  margin-top: 82px;
  padding: 56px 66px;
  background: #313233;
  p {
    font-family: 'Montserrat';
    font-size: 25px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #ffffff;
    span {
      color: #fb6c03;
    }
  }
  .text-bottom-container {
    position: relative;
  }
  .text-bottom {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.69;
    letter-spacing: normal;
    color: #ffffff;
    position: absolute;
    bottom: 35px;
  }
}
